.about-page {
    font-family: 'Open Sans', sans-serif;
    color: #333;
    line-height: 1.6;
}

.about-hero {
    position: relative;
    height: 300px;
    background-image: url('../images/about.jpg');
    background-size: cover;
    background-position: center;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 2rem;
}

/* If you don't have the image yet, this will be a green gradient fallback */
.about-hero:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(135deg, #4CAF50, #2E7D32);
    z-index: -1;
}

.hero-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4);
    z-index: -1;
}

.about-hero h1 {
    font-size: 3rem;
    margin-bottom: 20px;
    font-family: 'Raleway', sans-serif;
    font-weight: 700;
    color: #eaffeb;
}

.about-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

.about-section {
    margin-bottom: 1rem;
}

.about-section h2 {
    font-size: 2rem;
    color: #2E7D32;
    margin-bottom: 1.5rem;
    position: relative;
    padding-bottom: 10px;
}

.about-section h2:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 60px;
    height: 3px;
    background-color: #4CAF50;
}

.services-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 2rem;
    margin-top: 2rem;
}

.service-card {
    background-color: #f9f9f9;
    border-radius: 8px;
    padding: 1.5rem;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.service-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

.service-icon {
    font-size: 2.5rem;
    margin-bottom: 1rem;
}

.service-card h3 {
    font-size: 1.3rem;
    color: #2E7D32;
    margin-bottom: 0.75rem;
}

.team-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 2rem;
    margin-top: 2rem;
}

.cta-section {
    text-align: center;
    padding: 3rem 2rem;
    background-color: #f5f5f5;
    border-radius: 12px;
    margin-top: 3rem;
}

.cta-button {
    background-color: #4CAF50;
    color: white;
    border: none;
    padding: 12px 24px;
    font-size: 1.1rem;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin-top: 1.5rem;
    font-weight: 600;
}

.cta-button:hover {
    background-color: #388E3C;
}

/* Add these styles to your About.css file */

.company-info {
  display: flex;
  justify-content: space-around;
  margin: 2rem 0 0 0;
  flex-wrap: wrap;
  background-color: #f9f9f9;
  padding: 1.5rem;
  border-radius: 8px;
}

.info-item {
  text-align: center;
  padding: 0 1rem;
  min-width: 150px;
}

.info-item h4 {
  color: #4a8f3e;
  margin-bottom: 0.5rem;
}

.team-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

.team-member {
  background: white;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
  padding: 1.5rem;
  text-align: center;
  transition: transform 0.3s ease;
}

.team-member:hover {
  transform: translateY(-5px);
}

.member-image {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  margin: 0 auto 1rem;
  overflow: hidden;
}

.placeholder-image {
  background-color: #e0e0e0;
  background-image: linear-gradient(45deg, #f0f0f0 25%, transparent 25%, transparent 75%, #f0f0f0 75%, #f0f0f0),
                    linear-gradient(45deg, #f0f0f0 25%, transparent 25%, transparent 75%, #f0f0f0 75%, #f0f0f0);
  background-size: 20px 20px;
  background-position: 0 0, 10px 10px;
}

.member-title {
  color: #4a8f3e;
  font-weight: 500;
  margin: 0.5rem 0;
}

.member-bio {
  font-size: 0.9rem;
  line-height: 1.5;
}

.why-us {
/* indent */
    padding: 0 0 0 80px;
    background-color: #f8fdf8;
}

.paragraph {
    margin-bottom: 1.5rem;
}

/* Responsive adjustments */
@media (max-width: 768px) {
    .about-hero {
        height: 200px;
    }
    
    .about-hero h1 {
        font-size: 2.5rem;
    }
    
    .services-grid, .team-grid {
        grid-template-columns: 1fr;
    }
    
    .about-section h2 {
        font-size: 1.75rem;
    }
}
